// HORIZONTAL CENTERING
@mixin centerHorizontal {
    margin-left: auto;
    margin-right: auto;
}
// ABSOLUTE CENTERING
@mixin centerAll {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

// MEDIA SIZES
@mixin mediaSize($size) {
    @if $size == tablet {
      @media (min-width: 768px) { @content; }
    } @else if $size == desktop {
      @media (min-width: 1280px) { @content; }
    } 
}